import React, {useState, useContext, useEffect} from 'react';
import classes from './Kanban.module.css';
import ExistingColumn from '../../components/Column/ExistingColumn/ExistingColumn';
import NewColumn from '../../components/Column/NewColumn/NewColumn';
import {DragDropContext, Droppable, Draggable} from 'react-beautiful-dnd';

import FullContext from '../context/context';
import useData from '../ownHook/data';

const onDragEnd = (result, lists, setLists) => {
	const {source, destination, type} = result;
	

	if (destination){
		if (type === 'task'){
			if( source.droppableId === destination.droppableId){ // ta sama kolumna
				const indexOfColumn = lists.findIndex((value) => (value.id === source.droppableId));
				const columns = [...lists];
				const tasks = [...columns[indexOfColumn].tasks];
				
				const [removed] = tasks.splice(source.index, 1);
				tasks.splice(destination.index, 0, removed);
				
				columns[indexOfColumn].tasks = [...tasks];
				setLists([...columns]);
			} else { // inna kolumna
				const indexSourceColumn = lists.findIndex((value) => (value.id === source.droppableId));
				const indexDestColumn = lists.findIndex((value) => (value.id === destination.droppableId));
				const columns = [...lists];

				const sourceTasks = [...columns[indexSourceColumn].tasks];
				const destTasks = [...columns[indexDestColumn].tasks];
				
				const [removed] = sourceTasks.splice(source.index, 1);
				destTasks.splice(destination.index, 0, removed);

				columns[indexSourceColumn].tasks = [...sourceTasks];
				columns[indexDestColumn].tasks = [...destTasks];
				setLists([...columns]);
			}
		} else { // type === 'column'
			console.log(result);
			const columns = [...lists];
			const [movedColumn] = columns.splice(source.index, 1);
			columns.splice(destination.index, 0, movedColumn);

			setLists([...columns]);


			return;
		}

	} else { //if (destination)
		return;
	}
}

const Kanban = () => {
console.log('Kanban Rendering');
	const context = useContext(FullContext);

	const [lists, setLists] = useState([]);

	const addColumn = (name) => {
		const columns = [...lists];
		columns.push({name: name, id: (name + new Date().getTime()), order: columns.length, tasks:[] });
		setLists([...columns]);
	};

	const removeColumn = (id) => {
		const columns = [...lists];
		const positionOfColumn = lists.findIndex((value) => (id === value.id));
		columns.splice(positionOfColumn, 1);
		setLists([...columns]);
	};

	const addTask = (id, name) => {
		const positionOfColumn = lists.findIndex((value) => (id === value.id));
		const columns = [...lists];
		const newTask = [...columns[positionOfColumn].tasks];

		newTask.push({name: name, id:(name + new Date().getTime()), order: columns[positionOfColumn].tasks.length});
		
		columns[positionOfColumn].tasks = [...newTask];	
		setLists([...columns]);
	};

	const removeTask = (idList, idTask) => {
		const positionOfColumn = lists.findIndex((value) => (idList === value.id));
		const columns = [...lists];
		const positionOfTask = columns[positionOfColumn].tasks.findIndex((value) => (idTask === value.id));
		const newTask = [...columns[positionOfColumn].tasks];

		newTask.splice(positionOfTask, 1);

		columns[positionOfColumn].tasks = [...newTask];
		setLists([...columns]);
	};

	const editColumnName = (id, newName) => {
		const positionOfColumn = lists.findIndex((value) => (id === value.id));
		const columns = [...lists];
		columns[positionOfColumn].name = newName;
		setLists([...columns]);
	}

	const editTaskName = (idList, newName, idTask) => {
		const positionOfColumn = lists.findIndex((value) => (idList === value.id));
		const columns = [...lists];
		const positionOfTask = columns[positionOfColumn].tasks.findIndex((value) => (idTask === value.id));
		const newTask = [...columns[positionOfColumn].tasks];

		newTask[positionOfTask].name = newName;

		columns[positionOfColumn].tasks = [...newTask];
		setLists([...columns]);
	}

	const {saveData, fatchData, sending, errorSend, responseSend, fatching, errorFatch, responseFatch} = useData();

	useEffect(() => {
		if (context.isAuth) {
			saveData(context.userId, lists, context.token);
		}
	}, [lists]);

	useEffect(() =>{
		if (context.isAuth) {
			fatchData(context.userId, context.token)
		}

	}, [context.isAuth]);

	useEffect(() => {
		
		if (responseFatch) {
			if (responseFatch.data) {
				console.log(responseFatch.data);
				const data = [...responseFatch.data];
				data.map(value => {
					if (!value.tasks) {
						value.tasks = [];
					}
				return false;

				});
				console.log(data);

				setLists(data);	
			}
		}
		
	},[responseFatch]);


	return(
		
		<div className={classes.mainBox} >
		{context.isAuth &&
			<React.Fragment>
			
				<DragDropContext onDragEnd={(result) => onDragEnd(result, lists, setLists)}>
					<Droppable droppableId='main' type='column' direction='horizontal'>
						{
							(provided, snapshot) => {
								return(
									<div className={classes.kanban} 
										{...provided.droppableProps}
										ref={provided.innerRef} 
										style={{
			               					//backgroundColor: snapshot.isDraggingOver ? '#F9F3E6;' : '#F9F3E6;',
			               					//paddingBottom: snapshot.isDraggingOver ? '25%' : '0px'

			               				}}>

										{ lists.map( (list, index) => (
											<Draggable key={list.id} draggableId={list.id} index={index} >
												{(provided, snapshot) => {
                									return(
														<div className={classes.column}
															{...provided.draggableProps} 
                        									ref={provided.innerRef}
                        									
									                        style={{
									                        	...provided.draggableProps.style,
									                        	userSelect: 'none',
									                        	backgroundColor: snapshot.isDragging ? '#fba883' : '#EA9772',

									                        }}
									                    >
															<ExistingColumn
																dragHandleProps =  {provided.dragHandleProps}
																key={list.id} 
																identy={list.id} 
																name={list.name} 
																tasks={list.tasks} 
																addTask={addTask} 
																removeTask={removeTask}
																removeColumn={removeColumn}
																editColumnName={editColumnName}
																editTaskName={editTaskName}

															/>
															
														</div>
													)
												}}
											</Draggable>
										  )  ) }
										
										
										{provided.placeholder}
									<div className={classes.columnAdd} >
										<div className={classes.itemNew}>
											<NewColumn add={addColumn}/>
										</div>
									</div>
									</div>
								);
							}
						}	
					</Droppable>
					
				</DragDropContext>
				<div className={classes.infoBox}>
					{sending && 'Zapisuje zmiany...'}
					{responseSend && 'Zapisano zmiany'}
					{errorSend && <span style={{color: 'red'}}>'Zmian nie udało się zapisać!'</span>}
					{fatching && 'Pobieram Dane...'}
					{responseFatch && 'Dane Pobrane'}
					{errorFatch && 'Danych nie udało się pobrać'}

				</div>	
			</React.Fragment>		
		}	
		</div>
		
	);
};

export default React.memo(Kanban);